
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

export class UtilService{
    private activeLevel  = new Map<string,BehaviorSubject<string>>();  
    public setActiveLevel(childId,activeParentId){
        console.log("childId,activeParentId ",childId,activeParentId)
        let activeLevelObv  =  this.activeLevel.get(childId.toString())
        
        if (activeLevelObv === undefined){
            activeLevelObv = new BehaviorSubject(activeParentId)
            this.activeLevel.set(childId.toString(),activeLevelObv)
        }else{
            activeLevelObv.next(activeParentId)
            this.activeLevel.set(childId.toString(),activeLevelObv)
        }
    }

    public getActiveLevel(childId) {
        
        let levelOb = this.activeLevel.get(childId.toString())
        return levelOb.asObservable(); 
    }
}
