import { Component,OnInit } from '@angular/core';
import {ApiService} from './shared/service/api.service'
import {Levels,ProcMeta} from './shared/service/Interfaces'
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-root',
  templateUrl: './root.component.html',
  styleUrls: ['./app.component.scss']
})
export class RootComponent implements OnInit {
  title = 'proc-watch-ui';
  id = null
  process_name = null
  levels:Map<number,Levels> = null
  btTitle = "show more"
  isShow:boolean= false;
 
  proc_url='processmeta'
  proc_meta_list:Array<ProcMeta>
 
  constructor(private route: ActivatedRoute,private apiService:ApiService) {
    console.log("consutructor")
    this.route.params.subscribe((v) => {
      //this.params = v;
      this.fetch_proc_meta_data(v['id'])
    })
  }
  ngOnInit(){
      console.log("root component")
    //this.getParamValues();
    //this.fetch_proc_meta_data();
  }

  fetch_proc_meta_data(proc_id){
    this.title = this.title+" "+proc_id;
    console.log("procid " ,proc_id)
    this.apiService.make_api_call(this.proc_url, {"proc_id":proc_id}).subscribe(d => {
      console.log(d)
      this.id = proc_id;
      this.process_name = d['proc_name']
      this.levels = d['levels']
      console.log(this.levels)
      //this.proc_meta_list=d
    });
  }


  hide_show(){
 

   this.isShow = !this.isShow
   console.log("hide_Show ",this.isShow)
   if(this.isShow){
     this.btTitle = "hide"
   }else{
     this.btTitle = "show more"
   }
  }
  onItemClick(item){
    console.log("onItemClick ",item)
  }
  
}
