import { Component,OnInit, Input,Output ,EventEmitter} from '@angular/core';
import {ApiService} from '../shared/service/api.service'
import {ProcessStepData,ProcMeta, Meta} from '../shared/service/Interfaces'
import { copyStyles } from '@angular/animations/browser/src/util';
import {UtilService} from '../shared/utilservice'


@Component({
  selector: 'level-info',
  templateUrl: './level.info.html',
  styleUrls: ['./process.scss']
})
export class LevelInputCompoent implements OnInit {
 @Input() id
 @Input() lable:string
 @Input() disable:boolean
 @Input() type:string
 @Input() dependent_id:number
 lbvalue:any ;
 @Output() onLevelChange =new EventEmitter(true);
 @Input() set values(values:Map<number,Map<number,string>>){
     console.log("type ",this.type)
     console.log("this.id ",this.id)
     console.log("dependent_id ",this.dependent_id)
     if (this.type==='input'){
           let c:Map<number,string> = values['null']
           var keys=Object.keys(c)
           var vals = keys.map(function(key) {
           return c[key];

        });
           console.log(keys)
           this.utilser.setActiveLevel(this.dependent_id,keys[0])
          
           this.lbvalue= vals[0]
           
           
     }else{
        this.utilser.getActiveLevel(this.id).subscribe(val=>{
            console.log("ac",val)
            if(val ==='all'){
                let onjson = {}
                Object.keys(values).forEach(key=> {
                    onjson = Object.assign(onjson, values[key])
                    
                });
                console.log("values all",onjson)
                this.lbvalue = onjson
            }else{
                this.lbvalue= values[val]
                console.log("values ",this.lbvalue)
            }           
        })
        this.utilser.setActiveLevel(this.dependent_id,"all")
     }
    
 }
 

 //elements:Array<ProcessStepData>;
 //title = 'proc-watch-ui';
 
 
 constructor(private apiService:ApiService,private utilser:UtilService){

  }
  ngOnInit(){
    //this.fetch_proc_meta_data();
    //this.elements = ELEMENT_DATA;
  }
  onChange($event){
      console.log($event);
      console.log("selected val ",$event.target.value);
      this.utilser.setActiveLevel(this.dependent_id,$event.target.value)
      console.log("depid ",this.dependent_id)
      let v = $event.target.value
      if(this.dependent_id === -1){
          console.log("enable button and fetch ",v )
          if(v ==='all')
                this.onLevelChange.emit({'parent_id':v,'disable':true})
          else
             this.onLevelChange.emit({'parent_id':v,'disable':false})
      }
  }
}