import { Injectable } from '@angular/core';
import { Headers, Http, Response } from '@angular/http';
import { environment } from '../../../environments/environment';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs';
import * as $ from "jquery";
import { CookieService } from 'ngx-cookie-service';

@Injectable()
export class ApiService {

	private apiGetUrl = "/apiwatch/";
	private apiUrlPost = "/apiwatch/";
	public cookieName = "ct_token";

	constructor(private http: Http, private cookieService: CookieService) { }

	private _handle_error(error: any) {
		if (error.status == 401 || error.status == 302 || error.status == 10 || error.status == 403) {
			window.localStorage.clear();
			window.sessionStorage.clear();
			if ('_body' in error) {
				var link = error._body;
				if (link.indexOf('http') !== -1) {
					location.href = link;
				}
			}
			else {
				location.reload();
			}
			location.reload();
		};
		let message: string = (error.message) ? error.message : error.status ? `${error.status} - ${error.statusText}` : 'Server error';
		return Observable.throw(message);
	}
	private _read_response(res: Response): object {
		let response = res.json();
		if ('Result' in response) {
			return response.Result;
		}
		return response;
	}

	public make_api_call(url: string, params: object = {}): Observable<any> {
		let headers = new Headers();
		this.createAuthorizationHeader(headers);
		url = environment.origin+environment.api + url;
		return this.http.post(url, params, { headers: headers })
			.map(this._read_response)
			.catch(this._handle_error);
	};

	public downloadFile(urlStr: string, name: string) {
		if (name === 'not-available' || name === '' || name === undefined) {
			alert("Something went wrong please try again");
			return;
		}
		var params = $.param({
			fname: name
		});

		const url = urlStr + '?' + params;
		if ($('#download-frame')) {
			$('#download-frame').attr('src', ' ');
			$('#download-frame').remove('iframe');
		}
		$('body').append('<iframe id="download-frame" style="width:0;height:0;" src="' + url + '"></iframe');
		setTimeout(function () {
			$('#download-frame').attr('src', ' ');
			$('#download-frame').remove('iframe');
		}, 10000);
	}

	public download(name: string) {
		if (name === 'not-available') {
			alert("Something went wrong please try again");
			return;
		}
		var params = $.param({
			name: name
		}),
			url = environment.origin + environment.api + 'sendfresponse' + '?' + params;
		if ($('#download-frame')) {
			$('#download-frame').attr('src', ' ');
			$('#download-frame').remove('iframe');
		}
		$('body').append('<iframe id="download-frame" style="width:0;height:0;" src="' + url + '"></iframe');
		setTimeout(function () {
			$('#download-frame').attr('src', ' ');
			$('#download-frame').remove('iframe');
		}, 10000);
	}

	////////////////////////////////////

	createAuthorizationHeader(headers: Headers) {
		var token = "";
		if (this.cookieService.get(this.cookieName)) {
			token = this.getCookie();
		} else {
			token = this.getToken();
		}

		if (token !== "") {
			headers.append('token', token);
		}
		else {
			headers.append('token', 'false');
		}
	}

	getemptyapi(val, context) {
		return this.http.get(this.apiGetUrl, { params: { 'input': val, context: context } })
			.map((res: Response) => res.json());
	}

	getapi(url) {
		let headers = new Headers();
		this.createAuthorizationHeader(headers);
		return this.http.get(this.apiGetUrl + url, { headers: headers })
			.map((res: Response) => res.json())
			.catch(this._handle_error);
	}

	postapi(url, params) {
		let headers = new Headers();
		this.createAuthorizationHeader(headers);
		return this.http.post(this.apiUrlPost + url, params, { headers: headers })
			.map((res: Response) => res.json())
			.catch(this._handle_error);
	}

	applyDownload(apiKey, fname) {
		var url = environment.origin + environment.api + apiKey + '?fname=' + fname;
		if ($("#download-frame").length == 0) {
			$('body').append('<iframe id="download-frame" style="width:0;height:0;visibility:hidden;display:none;" src="' + url + '"></iframe');
		} else {
			$('#download-frame').attr('src', ' ');
			$('#download-frame').remove('iframe');
			$('body').append('<iframe id="download-frame" style="width:0;height:0;visibility:hidden;display:none;" src="' + url + '"></iframe');
		}
		return true;
	}

	getToken() {
		const token = this.getTokenFromURL();//sessionStorage.getItem('ct_token');//this.getCookie('token4sparex');
		window.localStorage.setItem(this.cookieName, token);
		this.setCookie(token);
		return token;
	}

	public getTokenFromURL() {
		const eolltbURL: string = window.location.href;
		let ct_token = "";
		ct_token = eolltbURL.split('ct_token=')[1];
		return ct_token;
	}

	// public getCookie(name: string) {
	// 	const ca: Array<string> = document.cookie.split(';');
	// 	const caLen: number = ca.length;
	// 	const cookieName = `${name}=`;
	// 	let c: string;

	// 	for (let i = 0; i < caLen; i += 1) {
	// 		c = ca[i].replace(/^\s+/g, '');
	// 		if (c.indexOf(cookieName) === 0) {
	// 			return c.substring(cookieName.length, c.length);
	// 		}
	// 	}
	// 	return '';
	// }

	public getCookie() {
		let token = this.cookieService.get(this.cookieName);
		return token;
	}

	public setCookie(value) {
		this.cookieService.set(this.cookieName, value);
	}

}
