import {Component,ElementRef, Input,Output,EventEmitter,HostListener,Renderer} from '@angular/core'
import { e } from '@angular/core/src/render3';
import { timer } from "rxjs";
import {StepMeta} from '../shared/service/Interfaces'

@Component({
  selector: '[item-node]',
  styleUrls: ['treenode.css'],
  template: `
  <td>{{step.step_name}}</td>
  <td>{{step.timer}}</td>
  <td><div class="circle" [ngClass]="{
    'red':!finished,
    'green':finished
}"></div></td>
`
})
export class ItemNode {
  @Input() step;
  @Input() id;
  @Output() onStepSubmit = new EventEmitter(true);
  isClick:boolean=false;
  subscription =null
  time = 0;
  finished =false;
  


  constructor(private el: ElementRef, private renderer: Renderer){
    
  }
  
  
  starttimer(elem){
    //console.log("starttimer ",elem)
       
    let d1 = new Date().toUTCString();
    if(this.isClick){
      this.finished =false;
      elem['start_time'] = d1;
      //console.log("cliecked ",elem)
      this.subscription = timer(0, 1000).subscribe(ec => {
        this.time ++;
        //console.log(this.time)
        elem['timer'] = this.getDisplayTimer(this.time);
      });
   }else{
      
      this.finished =true;
      this.time =0;
      elem['end_time'] = d1;
      this.subscription.unsubscribe();
      this.onStepSubmit.emit(elem); 
   }
   
  }
  getDisplayTimer(time: number) {
    const hours = '0' + Math.floor(time / 3600);
    const minutes = '0' + Math.floor(time % 3600 / 60);
    const seconds = '0' + Math.floor(time % 3600 % 60);

    return hours.slice(-2, -1)+''+hours.slice(-1)+':'+minutes.slice(-2, -1)+''+minutes.slice(-1)+':'+seconds.slice(-2, -1)+''+seconds.slice(-1)

      
  }
  
  
  @HostListener('click', ['$event']) onClick($event){
    console.info('clicked: ' + $event);
    this.isClick = !this.isClick
    this.renderer.setElementClass(this.el.nativeElement, 'active', this.isClick);
    this.starttimer(this.step)

}
 
}
